html,
body,
.game-window {
  height: 100%;
  width: 100%; }

.game-window {
  background: url("../images/668672.jpg") center center;
  background-size: cover; }

.game-header-primary {
  --gradient-opacity: .95;
  background: linear-gradient(rgba(9, 15, 22, var(--gradient-opacity)), rgba(18, 29, 43, var(--gradient-opacity)));
  border-bottom: 1px solid #1d2b35;
  border-top: 1px solid #02040f;
  box-shadow: 0 1px 0 0 #02040f, inset 0 1px 0 0 #12181f;
  backdrop-filter: blur(3px); }

.game-header-secondary {
  background: rgba(8, 12, 20, 0.95);
  backdrop-filter: blur(3px); }

.game-header .nav-menu {
  display: flex;
  font-weight: bold;
  font-size: 16px;
  text-transform: uppercase; }
  .game-header .nav-menu .menu-item {
    font-family: eurostile-extended;
    font-weight: 500;
    border-right: 1px solid #1d2b35; }
    .game-header .nav-menu .menu-item.is-current, .game-header .nav-menu .menu-item:hover {
      box-shadow: inset 0 0 0px 1px rgba(26, 51, 80, 0.8), inset 0 0 25px 1px rgba(26, 51, 80, 0.2);
      position: relative;
      transition: .4s;
      cursor: pointer; }
      .game-header .nav-menu .menu-item.is-current a, .game-header .nav-menu .menu-item:hover a {
        text-shadow: 0 0 5px #194fac, 0 0 3px #194fac;
        color: white; }
    .game-header .nav-menu .menu-item.is-disabled a,
    .game-header .nav-menu .menu-item.is-disabled a:hover {
      color: var(--color-text-disabled);
      text-shadow: none; }
    .game-header .nav-menu .menu-item.is-disabled:hover {
      box-shadow: none; }
    .game-header .nav-menu .menu-item a {
      color: var(--color-text-light);
      padding: .75em 1.5em;
      display: inline-block; }
  .game-header .nav-menu .menu-item-current-bounds {
    position: absolute;
    width: calc(100% + 5em);
    left: -2.5em;
    top: 0;
    height: 100%;
    overflow: hidden;
    mix-blend-mode: lighten;
    transition: .4s; }
    .menu-item:not(.is-current) .game-header .nav-menu .menu-item-current-bounds:hover {
      transition: .4s; }
      .menu-item:not(.is-current) .game-header .nav-menu .menu-item-current-bounds:hover:before {
        opacity: 1; }
      .menu-item:not(.is-current) .game-header .nav-menu .menu-item-current-bounds:hover:after {
        transform: translateY(50px); }
    .menu-item:not(.is-current) .game-header .nav-menu .menu-item-current-bounds:before {
      opacity: 0; }
    .menu-item:not(.is-current) .game-header .nav-menu .menu-item-current-bounds:after {
      transform: translateY(150px); }
    .game-header .nav-menu .menu-item-current-bounds:before {
      content: '';
      display: block;
      bottom: 0;
      height: 1px;
      width: 100%;
      background: linear-gradient(to right, rgba(20, 77, 163, 0) 0%, #97ebf9 50%, rgba(20, 77, 163, 0) 100%);
      position: absolute; }
    .game-header .nav-menu .menu-item-current-bounds:after {
      content: '';
      display: block;
      top: 0;
      left: 10%;
      width: 80%;
      height: 150%;
      position: absolute;
      background: #2066d0;
      border-radius: 50%;
      filter: blur(25px);
      transform: translateY(50px); }
